@import url(https://fonts.googleapis.com/css2?family=DM+Mono:wght@300;400;500&display=swap);
.movies {
    padding-top: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-left: 100px;
}

.movie {
    background-color: #ffffff;
    width: 40%;
    display: flex;
    margin-bottom: 70px;
    border-radius: 10px;
    transition: -webkit-transform 1000ms 100ms;
    transition: transform 1000ms 100ms;
    transition: transform 1000ms 100ms, -webkit-transform 1000ms 100ms;
}

.movie a {
    display: flex;
    text-decoration: none;
    color: #145374;
    padding: 20px
}

.movie:hover {
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie_data {
    padding: 20px;
}

.movie_title {
    font-size: 20px;
    margin: 0;
    padding: 14px 0;
    font-weight: 500;
}

.movie_year,
.movie_genres {
    font-size: 16px;
    font-weight: 300;
    padding-bottom: 8px;
    margin: 0;
    list-style: none;
    font-weight: 500;
    color: #145374;
}

.movie img {
    height: 250px;
    top: -20%;
    position: relative;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie_genres {
    display: flex;
    flex-wrap: wrap;
}

.movie_genres li {
    margin-right: 10px;
}

.movie_summary {
    font-size: 14px;
}

@media (max-width: 640px) {
    .movies {
        margin: 0 10px;
        padding: 0;
        padding-top: 100px;
    }

    .movie {
        width: 100%;
        margin-bottom: 100px;
    }

    .movie a {
        display: grid;
    }

    .movie img {
        height: 400px;
        margin: 0 auto;
        top: -15%;
    }

    .movie_data {
        position: relative;
        top: -20%;
        padding: 0;
    }

    .movie_title {
        font-size: 24px;
        text-align: center;
    }

    .movie_year {
        text-align: center;
        padding-bottom: 5px;
    }

    .movie_genres {
        justify-content: center;
    }

    .movie_genres li {
        margin: 0 5px;
    }

}
.loader {
    height: 100vh;
    display: grid;
    place-content: center;
}
.About {
    height: 100vh;
}
.About .container {
    background-color: #ffffff;
    width: 40%;
    padding: 10px;
    border-radius: 10px;
    font-size: 20px;
    line-height: 2rem;
}

@media (max-width: 640px) {
    .About .container {
        width: 90%;
        font-size: 16px;
    }
}
.Navigation {
    display: grid;
    grid-row-gap: 20px;
    row-gap: 20px;
    position: fixed;
    width: 100px;
    background-color: #00334e;
    padding: 20px 0;
    border-radius: 10px;
    top: 15px;
    place-content: center;
}

a {
    text-decoration: none;
    color: #ffffff;
}

.icon {
    border-radius: 50%;
    padding:10px;
    box-shadow: -2px -2px 3px rgba(255, 255,255, 0.4),
    3px 3px 3px rgba(0, 0, 0, 0.2);
}

.icon span {
    display: flex;
    justify-content: center;
}

.icon:hover{
    text-shadow: 0 0 5px #ffffff;
}

@media (max-width: 640px) {
    .Navigation {
        z-index: 1;
        font-size: 14px;
        width: 80px;
    }
}
.Detail {
    padding: 100px
}
.Detail .container {
    width: 60%;
    display: grid;
    background-color: #ffffff;
    padding: 10px;
    border-radius: 10px;
}

.detail_title {
    display: flex;
    font-size: 36px;
    font-weight: 500;
    position: relative;
    margin: 0 auto;
    top: -90%;
    text-shadow: 0px 2px 4px rgba(50, 50, 93, 0.5);
}

.detail_data {
    display: flex;
    justify-content: space-around;
}

.detail_data > * {
    padding: 30px;
}

.detail_data img {
    max-width: 100%;
    align-self: center;
}

.detail_summary {
    line-height: 1.5rem;
    text-indent: 30px;
    word-break: keep-all;
}

@media (max-width: 640px) {

    .Detail {
        padding: 60px 0;
    }

    .Detail .container {
        width: 90%;
    }

    .detail_title {
        top: -90%;
    }

    .detail_data {
        display: grid;
    }

    .detail_data img {
        width: 70%;
        margin: 0 auto;
    }
}
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
* {
    box-sizing: border-box;
    text-decoration: none;
}

body,
.Home,
.About,
.Detail {
    background-color: #e8e8e8;
    font-family: 'DM Mono', monospace;
    color: #00334e;
}

.About,
.Detail {
    display: flex;
    justify-content: center;
    align-items: center;
}
